:root[data-theme="dark"] {
  --page-bgc: #111;
  --hero-bgc-clr: #282828;
  --hero-title-clr: #fff;
  --hero-subtract-clr-desk: url("subtract_black.73ee6931.png");
  --hero-subtract-clr-tab: url("subtract_black_tablet.112eec2d.png");
  --hero-subtract-clr-mob: url("subtract_black_mob.97de5d65.png");
  --hero-slider-btn: #b7b7b7;
  --hero-slider-arrow: #f8f8f8;
  --primary-text-clr: #fff;
  --secondary-text-clr: #f8f8f8;
  --muted-text-clr: #b7b7b7;
  --secondary-bcg-clr: #fff;
  --muted-secondary-text-clr: #b7b7b7;
  --modal-text-color: #111;
  --modal-secondary-color: #f8f8f8;
  --switcher-pos-mob: 18px;
  --switcher-pos-tab: 30px;
  --switcher-pos-desk: 34px;
  --switcher-bow-pos-mob: -16px;
  --switcher-bow-pos-tab: -4px;
  --switcher-bow-pos-desk: 0px;
  --switcher-bow-rotaion: 0;
  --mobile-menu-bgc: #111;
  --backdrop-bgc: #fff;
}

:root[data-theme="light"] {
  --page-bgc: #fff;
  --hero-bgc-clr: #f8f8f8;
  --hero-title-clr: #111;
  --hero-subtract-clr-desk: url("subtract_white.6e4f549b.png");
  --hero-subtract-clr-tab: url("subtract_white_tablet.1275fdd5.png");
  --hero-subtract-clr-mob: url("subtract_white_mob.426a7ebb.png");
  --hero-slider-btn: #282828;
  --hero-slider-arrow: #595959;
  --primary-text-clr: #282828;
  --secondary-text-clr: #282828;
  --muted-text-clr: #595959;
  --secondary-bcg-clr: #f8f8f8;
  --muted-secondary-text-clr: #111;
  --modal--text-color: #282828;
  --modal-secondary-color: #111;
  --switcher-pos-mob: -3px;
  --switcher-pos-tab: -3px;
  --switcher-pos-desk: -2px;
  --switcher-bow-pos-mob: -36px;
  --switcher-bow-pos-tab: -36px;
  --switcher-bow-pos-desk: -35px;
  --switcher-bow-rotaion: 180deg;
  --mobile-menu-bgc: #f8f8f8;
  --backdrop-bgc: #000;
}

