.header__theme-switcher {
  cursor: pointer;
  width: 38px;
  height: 16px;
  background: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  border-radius: 194px;
  align-items: center;
  gap: 14px;
  padding: 5px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header__theme-switcher {
    width: 54px;
    height: 20px;
    gap: 24px;
    margin-left: auto;
  }
}

@media screen and (min-width: 1280px) {
  .header__theme-switcher {
    width: 60px;
    height: 24px;
    gap: 24px;
  }
}

.header__theme-switcher:before {
  content: "";
  z-index: 2;
  width: 12px;
  height: 12px;
  transform: translateX(var(--switcher-pos-mob) );
  background-color: #fff;
  border-radius: 50px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 12%;
}

@media screen and (min-width: 768px) {
  .header__theme-switcher:before {
    width: 16px;
    height: 16px;
    transform: translateX(var(--switcher-pos-tab) );
  }
}

@media screen and (min-width: 1280px) {
  .header__theme-switcher:before {
    width: 18px;
    height: 18px;
    transform: translateX(var(--switcher-pos-desk) );
  }
}

.header__theme-switcher:after {
  content: "";
  z-index: 2;
  width: 10px;
  height: 10px;
  transform: translateX(var(--switcher-bow-pos-mob) ) rotate(var(--switcher-bow-rotaion) );
  background-color: rgba(0, 0, 0, 0);
  border: 0 solid #f87719;
  border-right-width: 2px;
  border-radius: 50px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 17%;
  left: 40px;
}

@media screen and (min-width: 768px) {
  .header__theme-switcher:after {
    width: 14px;
    height: 14px;
    transform: translateX(var(--switcher-bow-pos-tab) ) rotate(var(--switcher-bow-rotaion) );
    top: 19%;
  }
}

@media screen and (min-width: 1280px) {
  .header__theme-switcher:after {
    width: 15px;
    height: 15px;
    transform: translateX(var(--switcher-bow-pos-desk) ) rotate(var(--switcher-bow-rotaion) );
    top: 19%;
  }
}

